@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
body {
  font-family: 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}


.mainApp{
  width: 100%;
  height: 100vh;
  overflow: hidden;
  display: flex;
  flex-direction: row;
  position: relative;
}

.sideNav{
  position: relative;
  display: flex;
  width: 100px;
  min-width: 100px;
  height: 100%;
  background-color: #000;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
}

.sideNavLogo h1{
  color: #fff;
  font-weight: 900;
  font-size: 3rem;
  opacity: .3;
  text-transform: uppercase;
}

.sideNavBttns{
  height: 25vh;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  grid-gap: 30px;
  gap: 30px;
  color: rgb(88, 88, 88);
}
.mainNavbar{
    position: relative;
    width: 100%;
    height: 100px;
    min-height: 100px;
    z-index: 10;
    color: #fff;
    display: flex;
    align-items: center;
    flex-direction: row-reverse;
}

.mainNavbar ul{
    width: 50%;
    max-width: 700px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    list-style: none;
    padding-right: 50px;
    font-weight: 700;
    letter-spacing: 1px;
}

.mainNavbar ul li{
    cursor: pointer;
    position: relative;
}

.mainNavbar ul li::before{
    content: "";
    position: absolute;
    width: 0;
    height: 3px;
    background-color: rgb(255, 5, 5);
    bottom: -10px;
    transition: .2s;
}

.mainNavbar ul li:hover::before{
    width: 100%;
}

.mainNews{
    position: relative;
    width: 100%;
    height: 25vh;
    min-height: 25vh;
    z-index: 10;
    background: linear-gradient(to left, #000, #000 50% ,transparent);
    color: #fff;
    display: flex;
}

.mainNews > * {
    width: 50%;
    height: 100%;
    position: relative;
}

.upcomingEvents{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    padding: 30px;
}

.upcomingEvents h5{
    width: 100%;
    font-size: 5vh;
    color: transparent;
    -webkit-text-stroke: 1px #fff;
}

.event{
    width: 50%;
    display: flex;
    flex-direction: column;
}

.event a{
    text-decoration: none;
    color: #f00;
    margin-top: 10px;
}

.newsDiv{
    display: flex;
}

.newsDiv img{
    position: relative;
    width: 33%;
    height: 100%;
    object-fit: cover;
    flex: 1 1;
    opacity: .3;
    transition: .2s;
    cursor: pointer;
}

.newsDiv img:hover{
    opacity: .5;
}

.active{
    opacity: 1 !important;
    border: solid 1px red;
}
.mainHero{
    flex: 1 1;
    background-color: #131313;
    position: relative;
    display: flex;
    justify-content: space-between;
    flex-direction: column;
}

.mainHero > img{
    position: absolute;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.mainHero::before{
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.623);
    z-index: 1;
}

.contents{
    z-index: 10;
    flex: 1 1;
    display: flex;
    justify-content: center;
    align-items: center;
}

.contents > * {
    width: 50%;
    padding: 100px;
    color: #fff;
}

.contentsText h2{
    font-weight: 900;
    font-size: 5vw;
}

.contentsText p{
    font-size: 1.8rem;
}

.contentsText button{
    padding: 10px 30px;
    margin-top: 15px;
    color: #fff;
    font-size: 1.1rem;
    border: solid 1px red;
    border-radius: 30px;
    background-color: transparent;
    cursor: pointer;
}

.contentsSwap{
    display: flex;
    flex-direction: column;
    position: relative;
}

.contentsSwap span{
    font-size: 1.2rem;
    font-weight: 600;
    color: rgb(68, 68, 68);
}

.contentsSwap span:first-of-type{
    margin-top: 50px;
}

.activeText{
    color: #fff !important;
}

.contentsSwap h1{
    width: 50%;
    position: relative;
}

.contentsSwap h1::before{
    content: "";
    position: absolute;
    width: 100%;
    height: 4px;
    bottom: -10px;
    background-color: rgb(56, 55, 55);
}

.contentsSwap h1::after{
    content: "";
    position: absolute;
    width: 0;
    height: 4px;
    bottom: -10px;
    left: 0;
    animation: loading 5s infinite linear;
    background-color: red;
}

@keyframes loading{
    0% {
        width: 0;
    }
    100% {
        width: 100%;
    }
}


